import React from "react";
import { menuData } from "./data";
import "./IndexData.css";
import styled from "styled-components";

export const IndexDatas = () => {
  return (
    <>
      <HomePageHeader />
      <div>
        {menuData.map((data, key) => {
          return (
            <div key={key}>
              <IndexData
                key={key}
                title={data.title}
                pizzaName={data.pizzaName}
                desc={data.desc}
                price={data.price}
              />
            </div>
          );
        })}
      </div>
    </>
  );
};

const HomePageHeader = () => {
  return (
    <header>
      <h2>welcome</h2>
    </header>
  );
};

const IndexData = ({ title, pizzaName, desc, price }) => {
  if (!title) return <div />;
  return (
    <Grid>
      <Card>
        <CardTitle>{title}</CardTitle>
        <CardText>
          <div>{pizzaName}</div>
          {desc}
        </CardText>
      </Card>
      <Card>
        <CardTitle>{title}</CardTitle>
        <CardText>
          <div>{pizzaName} </div>
          {desc}
        </CardText>
      </Card>
      <Card>
        <CardTitle>{title}</CardTitle>
        <CardText>
          <div>{pizzaName} </div>
          {desc}
        </CardText>
      </Card>
    </Grid>
  );
};

const Grid = styled.div`
  display: grid;
  max-width: 1100px;
  margin: auto;
  margin-top: 5rem;
  margin-bottom: 5rem;
  grid-gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
`;

const Card = styled.div`
  margin: auto;
  padding: 16px;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  justify-content: center;
`;

const CardTitle = styled.h3`
  text-align: center;
`;

const CardText = styled.p`
  font-size: 1.5rem;
  padding: 2.5rem;
  text-align: center;
  border-radius: 3px;
  transition: transform 0.3s;
`;
